/* src/components/Topbar.css */

.topbar {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  background: #fff;
  border-bottom: 1px solid #f0f0f0;
  position: sticky;
  top: 0;
  z-index: 100;
}

.content-button{
  justify-content: center;
  align-items: center;
  height: 100%;
}

.search-button {
  height: 100%;
  align-items: center;
  padding: 0px 12px;
  background-color: transparent;
  border: none;
  display: flex;
  color: #232837;
  cursor: pointer;
  font-size: 15px;
  font-weight: 500;
  transition: background-color 300ms ease;
}

.search-button:hover {
  background-color: #f0f0f0;
}